"use client"

import { useState, useEffect } from "react"
import { useSearchParams } from "next/navigation"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod"
import * as z from "zod"

// Mock mutual funds data for dropdown
const availableFunds = [
  { id: "hdfc-top-100", name: "HDFC Top 100 Fund", minInvestment: 500 },
  { id: "sbi-bluechip", name: "SBI Blue Chip Fund", minInvestment: 1000 },
  { id: "icici-balanced", name: "ICICI Prudential Balanced Advantage Fund", minInvestment: 500 },
  { id: "axis-liquid", name: "Axis Liquid Fund", minInvestment: 1000 },
  { id: "mirae-emerging", name: "Mirae Asset Emerging Bluechip Fund", minInvestment: 500 },
  { id: "franklin-ultra-short", name: "Franklin India Ultra Short Bond Fund", minInvestment: 5000 }
]

// Form validation schema
const investmentSchema = z.object({
  fundId: z.string().min(1, "Please select a mutual fund"),
  amount: z.number().min(500, "Minimum investment amount is ₹500"),
  paymentMethod: z.enum(["netbanking", "upi", "debitcard"], {
    required_error: "Please select a payment method"
  }),
  investmentType: z.enum(["lumpsum", "sip"], {
    required_error: "Please select investment type"
  }),
  sipFrequency: z.enum(["monthly", "quarterly"]).optional(),
  sipDuration: z.number().optional()
})

type InvestmentForm = z.infer<typeof investmentSchema>

export default function Invest() {
  const searchParams = useSearchParams()
  const preSelectedFund = searchParams.get("fund")
  
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [showConfirmation, setShowConfirmation] = useState(false)
  const [submittedData, setSubmittedData] = useState<InvestmentForm | null>(null)

  const {
    register,
    handleSubmit,
    watch,
    setValue,
    formState: { errors },
    reset
  } = useForm<InvestmentForm>({
    resolver: zodResolver(investmentSchema),
    defaultValues: {
      fundId: preSelectedFund || "",
      investmentType: "lumpsum"
    }
  })

  const selectedFundId = watch("fundId")
  const investmentType = watch("investmentType")
  const amount = watch("amount")

  const selectedFund = availableFunds.find(fund => fund.id === selectedFundId)

  useEffect(() => {
    if (preSelectedFund) {
      setValue("fundId", preSelectedFund)
    }
  }, [preSelectedFund, setValue])

  const onSubmit = async (data: InvestmentForm) => {
    setIsSubmitting(true)
    
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 2000))
    
    setSubmittedData(data)
    setShowConfirmation(true)
    setIsSubmitting(false)
  }

  const handleNewInvestment = () => {
    setShowConfirmation(false)
    setSubmittedData(null)
    reset()
  }

  if (showConfirmation && submittedData) {
    const fund = availableFunds.find(f => f.id === submittedData.fundId)
    
    return (
      <div className="max-w-2xl mx-auto space-y-8">
        <div className="text-center space-y-2">
          <h1 className="text-3xl font-bold tracking-tight text-green-600">Investment Successful!</h1>
          <p className="text-muted-foreground">Your investment has been processed successfully</p>
        </div>

        <Card>
          <CardHeader>
            <CardTitle>Investment Summary</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid gap-4 md:grid-cols-2">
              <div>
                <p className="text-sm text-muted-foreground">Mutual Fund</p>
                <p className="font-medium">{fund?.name}</p>
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Investment Type</p>
                <p className="font-medium capitalize">{submittedData.investmentType}</p>
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Amount</p>
                <p className="font-medium">₹{submittedData.amount.toLocaleString()}</p>
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Payment Method</p>
                <p className="font-medium capitalize">{submittedData.paymentMethod.replace(/([A-Z])/g, ' $1')}</p>
              </div>
              {submittedData.investmentType === "sip" && (
                <>
                  <div>
                    <p className="text-sm text-muted-foreground">SIP Frequency</p>
                    <p className="font-medium capitalize">{submittedData.sipFrequency}</p>
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">SIP Duration</p>
                    <p className="font-medium">{submittedData.sipDuration} months</p>
                  </div>
                </>
              )}
            </div>

            <Alert>
              <AlertDescription>
                You will receive a confirmation email shortly with your investment details and transaction ID.
              </AlertDescription>
            </Alert>

            <div className="flex gap-4">
              <Button onClick={handleNewInvestment} variant="outline" className="flex-1">
                Make Another Investment
              </Button>
              <Button className="flex-1">
                View Portfolio
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    )
  }

  return (
    <div className="max-w-2xl mx-auto space-y-8">
      <div className="text-center space-y-2">
        <h1 className="text-3xl font-bold tracking-tight">Invest in Mutual Funds</h1>
        <p className="text-muted-foreground">
          Start your investment journey with our carefully selected mutual funds
        </p>
      </div>

      <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
        <Card>
          <CardHeader>
            <CardTitle>Investment Details</CardTitle>
            <CardDescription>Choose your fund and investment amount</CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            {/* Fund Selection */}
            <div className="space-y-2">
              <Label htmlFor="fundId">Select Mutual Fund *</Label>
              <Select value={selectedFundId} onValueChange={(value) => setValue("fundId", value)}>
                <SelectTrigger>
                  <SelectValue placeholder="Choose a mutual fund" />
                </SelectTrigger>
                <SelectContent>
                  {availableFunds.map((fund) => (
                    <SelectItem key={fund.id} value={fund.id}>
                      {fund.name} (Min: ₹{fund.minInvestment})
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              {errors.fundId && (
                <p className="text-sm text-red-600">{errors.fundId.message}</p>
              )}
            </div>

            {/* Investment Type */}
            <div className="space-y-3">
              <Label>Investment Type *</Label>
              <RadioGroup
                value={investmentType}
                onValueChange={(value) => setValue("investmentType", value as "lumpsum" | "sip")}
                className="flex space-x-6"
              >
                <div className="flex items-center space-x-2">
                  <RadioGroupItem value="lumpsum" id="lumpsum" />
                  <Label htmlFor="lumpsum">Lump Sum</Label>
                </div>
                <div className="flex items-center space-x-2">
                  <RadioGroupItem value="sip" id="sip" />
                  <Label htmlFor="sip">SIP (Systematic Investment Plan)</Label>
                </div>
              </RadioGroup>
              {errors.investmentType && (
                <p className="text-sm text-red-600">{errors.investmentType.message}</p>
              )}
            </div>

            {/* Investment Amount */}
            <div className="space-y-2">
              <Label htmlFor="amount">
                Investment Amount * 
                {selectedFund && (
                  <span className="text-sm text-muted-foreground ml-2">
                    (Min: ₹{selectedFund.minInvestment})
                  </span>
                )}
              </Label>
              <Input
                id="amount"
                type="number"
                placeholder="Enter amount"
                {...register("amount", { valueAsNumber: true })}
                min={selectedFund?.minInvestment || 500}
              />
              {errors.amount && (
                <p className="text-sm text-red-600">{errors.amount.message}</p>
              )}
              {selectedFund && amount && amount < selectedFund.minInvestment && (
                <p className="text-sm text-red-600">
                  Minimum investment for this fund is ₹{selectedFund.minInvestment}
                </p>
              )}
            </div>

            {/* SIP Options */}
            {investmentType === "sip" && (
              <div className="grid gap-4 md:grid-cols-2">
                <div className="space-y-2">
                  <Label htmlFor="sipFrequency">SIP Frequency *</Label>
                  <Select onValueChange={(value) => setValue("sipFrequency", value as "monthly" | "quarterly")}>
                    <SelectTrigger>
                      <SelectValue placeholder="Select frequency" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="monthly">Monthly</SelectItem>
                      <SelectItem value="quarterly">Quarterly</SelectItem>
                    </SelectContent>
                  </Select>
                  {errors.sipFrequency && (
                    <p className="text-sm text-red-600">{errors.sipFrequency.message}</p>
                  )}
                </div>

                <div className="space-y-2">
                  <Label htmlFor="sipDuration">SIP Duration (months) *</Label>
                  <Input
                    id="sipDuration"
                    type="number"
                    placeholder="e.g., 12"
                    {...register("sipDuration", { valueAsNumber: true })}
                    min={6}
                    max={240}
                  />
                  {errors.sipDuration && (
                    <p className="text-sm text-red-600">{errors.sipDuration.message}</p>
                  )}
                </div>
              </div>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Payment Method</CardTitle>
            <CardDescription>Choose your preferred payment method</CardDescription>
          </CardHeader>
          <CardContent>
            <RadioGroup
              onValueChange={(value) => setValue("paymentMethod", value as "netbanking" | "upi" | "debitcard")}
              className="space-y-3"
            >
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="netbanking" id="netbanking" />
                <Label htmlFor="netbanking">Net Banking</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="upi" id="upi" />
                <Label htmlFor="upi">UPI</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="debitcard" id="debitcard" />
                <Label htmlFor="debitcard">Debit Card</Label>
              </div>
            </RadioGroup>
            {errors.paymentMethod && (
              <p className="text-sm text-red-600 mt-2">{errors.paymentMethod.message}</p>
            )}
          </CardContent>
        </Card>

        {/* Investment Summary */}
        {selectedFund && amount && amount >= selectedFund.minInvestment && (
          <Card>
            <CardHeader>
              <CardTitle>Investment Summary</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <div className="flex justify-between">
                  <span>Fund:</span>
                  <span className="font-medium">{selectedFund.name}</span>
                </div>
                <div className="flex justify-between">
                  <span>Investment Type:</span>
                  <span className="font-medium capitalize">{investmentType}</span>
                </div>
                <div className="flex justify-between">
                  <span>Amount:</span>
                  <span className="font-medium">₹{amount.toLocaleString()}</span>
                </div>
                {investmentType === "sip" && (
                  <div className="flex justify-between">
                    <span>Total Investment:</span>
                    <span className="font-medium">
                      ₹{(amount * (watch("sipDuration") || 0)).toLocaleString()}
                    </span>
                  </div>
                )}
              </div>
            </CardContent>
          </Card>
        )}

        <Button 
          type="submit" 
          className="w-full" 
          size="lg"
          disabled={isSubmitting}
        >
          {isSubmitting ? "Processing Investment..." : "Invest Now"}
        </Button>
      </form>
    </div>
  )
}
